@media screen and (max-width: 768px) {
  .fa-chevron-down {
    transform: rotate(0deg);
    transition: all 0.6s;
    &.active {
      transform: rotate(180deg);
    }
  }

  .dropdown_menu--animated {
    li {
      display: block;
      opacity: 1;
    }
  }

  .dropdown_menu-6 {
    animation: growDown 300ms ease-in-out forwards;
    transform-origin: top center;
  }

  @keyframes growDown {
    0% {
      transform: scaleY(0);
    }
    100% {
      transform: scaleY(1);
    }
  }
}
